import React from 'react';
import './listitems.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

function Listitems (props) {
    const items = props.items;

    const litems = items.map(item =>{
        return  <div className="mylist" key={item.key}>
            <p>
                <input  type="text" id={item.key} value={item.text} onChange={(e) => {props.update(e.target.value,item.key)}}/>
                
                <span>
                    <button className="obut">OK</button>
                    <FontAwesomeIcon className="faicons" onClick={() => {props.deleteitem(item.key)}} icon="trash"  />
                </span>
            </p>

        </div>
    })

    return(
        <div>
            {litems}
        </div>
    )
} 

export default Listitems;