import React, { Component } from 'react';
import './main.css'
import Listitems from './listitems';
import { library } from '@fortawesome/fontawesome-svg-core'
import { faTrash } from '@fortawesome/free-solid-svg-icons'

library.add(faTrash)

class Main extends Component {
    state = {
        listitems:[],
    
        citems:{
          text:'',
          key:''
        }
    
      }
    
      handleinput = (e) => {
        this.setState({
          citems:{
            text:e.target.value,
            key: Date.now()
          }
        })
      }
    
      additem = (e) => {
    
        e.preventDefault();
    
        const nitems = this.state.citems;
    
        if(nitems.text !=="")
        {
          const listitems = [...this.state.listitems, nitems];
    
          this.setState({
            listitems:listitems,
            citems:{
              text:'',
              key:''
            }
          })
        }
      }
    
      
    
      deleteitem = (key) => {
        const filtereditems = this.state.listitems.filter(item => item.key!== key);
    
        this.setState({
          listitems:filtereditems
        })
      }
    
      update = (text,key) => {
    
        const nlitems = this.state.listitems;
    
        nlitems.map(item=>{    
    
          if(item.key===key){
            console.log(item.key +"    "+key)
            item.text= text;
          }
    
        })
    
        this.setState({
          listitems: nlitems
        })
      }
    
      render(){
        return (
          <div className="main">
            <div className="mainpage">
              <section>
                <form className="todoform" onSubmit={this.additem}>
                  <input type="text" name="input" placeholder="Enter Task....!!" value={this.state.citems.text} onChange={this.handleinput} />
                  <button type="submit">ADD</button>
                </form>
                <Listitems items={this.state.listitems} deleteitem={this.deleteitem} update={this.update} />
              </section>
            </div>
          </div>
        );
      }
    }


export default Main;